<template>
  <div id="SignList" class="font_sc">
    <VantList :options="pageOptions" ref="vantList">
      <template slot-scope="props" slot="nowList">
        <div class="box" ref="newConBox">
          <van-tabs v-model="active" @change="tabChange" sticky :offset-top="0" animated swipeable :type="type">
            <van-tab :title="v.title" v-for="(v, k) in tabList" :key="k">
              <div class="tab_box">
                <div class="question_detail_wrap">
                  <ul class="content list_text" v-if="!!props.list.length">
                    <li class="list" v-for="(item, index) in props.list" :key="index" @click="goDetail(item)">
                      <div v-html="item.content">
                        
                      </div>
                    </li>
                  </ul>
                  <div class="without" v-if="finished&&props.list.length==0">
                    <img src="@/assets/images/without.png" alt="" />
                    <div>暂无内容</div>
                  </div>
                </div>
              </div>
            </van-tab>
          </van-tabs>
        </div>
      </template>
    </VantList>
  </div>
</template>

<script>
import { fetchList } from "@/api/list";
export default {
  name: "SignList",
  data() {
    return {
      //tab
      type: "line",
      active: 0,
      tabList: [
        {
          title: "类别1",
          id: 0,
        },
        {
          title: "类别2",
          id: 1,
        },
        {
          title: "类别3",
          id: 2,
        },
      ],
      // 是否第一次进入
      isFirstEnter: true,
  
      pageOptions: {
        title: "",
        isTop: 1,
        catId: "",
        apiType: "getTextBookList"
      }
    };
  },

  beforeRouteEnter(to, from, next) {
    const fromName = ["mineAsk"];
    if (fromName.includes(from.name)) {
      // console.log(fromName, "fromName");
      to.meta.isBack = true;
      //判断是从哪个路由过来的，
      //如果是page2过来的，表明当前页面不需要刷新获取新数据，直接用之前缓存的数据即可
    }
    //继续执行
    next();
  },

  activated() {
    if (!this.$route.meta.isBack || this.isFirstEnter) {
      this.active = 0;
      this.refresh();
      // this.$refs.topInfo.offsetHeight;
    }
    // 恢复成默认的false，避免isBack一直是true，导致下次无法获取数据
    this.$route.meta.isBack = false;
    // 恢复成默认的false，避免isBack一直是true，导致每次都获取新数据
    this.isFirstEnter = false;
    // console.log(this.$route.meta.isBack, "activated");
  },

  created() {
    this.isFirstEnter = true;
    //新增界面滚动监听
    this.$nextTick(function () {
      window.addEventListener("scroll", this.handleScroll);
    });
    // 只有第一次进入或者刷新页面后才会执行此钩子函数，使用keep-alive后（2+次）进入不会再执行此钩子函数
  },

  beforeDestroy() {
    window.removeEventListener("scroll", this.handleScroll);
  },

  methods: {
    tabChange(val) {
      // console.log(val, "tabChange",this.active);
      this.finished = false;
      this.$refs['vantList'].refresh();
    },

    goDetail(record) {
      const { id } = record;
    },
  },
};
</script>

<style lang="scss" src='./index.scss' scoped>
</style>




